
.biglistener {
    background-color: #0c0d0f;

    position: absolute;
    height: 100%;
    width: 100%;
}

.large {
    display: flex;
    justify-content: center;
}

.score {
    position: relative;
    margin: auto;
    text-align: center;
    color: white;
}

.game-area {

    position: relative;
    
    margin: 20px;
    margin-top: 40px;

    width: 40vh;
    height: 40vh;
    border: 2px solid black;
}

.snake-dot {
    position: absolute;
    width: 4%;
    height: 4%;
    background-color: green;
    /* border: 2px solid #0c0d0f; */
    z-index: 2;
}

.snake-food {
    position: absolute;
    width: 4%;
    height: 4%;
    background-color: red;
    /* border: 2px solid #0c0d0f; */
    z-index: 1;
}